.text-center {
  text-align: center;
}

.bg-primary {
  background-color: #c3c4d7;
}

/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: #c3c4d7;
  font-size: 1.6rem;
}

p {
  color: #000;
  font-size: 2rem;
}

.container {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

h1,
h2,
h3 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3.2rem;
}

.header {
  background-image: linear-gradient(rgba(57, 70, 89, 0.7) 30%, rgba(57, 70, 89, 0.7) 50%), url(../img/workspace.jpg);
  /* background-image: url(../img/coffee.jpg); */
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top center;
  padding: 5rem 0;
}
@media (min-width: 768px) {
  .header {
    padding: 3rem 0;
  }
}
.header .logo {
  margin: 0 auto;
  max-width: 10rem;
}
.header .logo img {
  height: 8rem;
}
@media (min-width: 768px) {
  .header .logo {
    margin: 0;
  }
}
.header .container-header {
  padding: 8rem 0;
}
@media (min-width: 1024px) {
  .header .container-header {
    padding: 10rem 0;
  }
}
.header h1 {
  color: #ffffff;
  font-size: 4rem;
}
@media (min-width: 768px) {
  .header h1 {
    font-size: 5rem;
    max-width: 50%;
  }
}
@media (min-width: 1024px) {
  .header h1 {
    max-width: 50%;
  }
}
.header .button-start {
  background-color: #c3c4d7;
  font-size: 2rem;
  padding: 1.2rem 3rem;
  border-radius: 1rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #4a74ff;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
  /* @include tablet {
    width: auto;
  } */
  border: 2px solid grey;
  margin-top: 2rem;
  display: block;
}
.header .button-start:hover {
  cursor: pointer;
  background-color: #a4a6c3;
}
.header .button-practice {
  background-color: #c3c4d7;
  font-size: 2rem;
  padding: 1.2rem 3rem;
  border-radius: 1rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #7b7cb6;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
  /* @include tablet {
    width: auto;
  } */
  border: 2px solid grey;
  margin-top: 2rem;
  display: block;
}
.header .button-practice:hover {
  cursor: pointer;
  background-color: #a4a6c3;
}
@media (min-width: 768px) {
  .header .button-start,
.header .button-practice {
    max-width: 30%;
  }
}

.form {
  margin-top: 5rem;
  margin-bottom: 10rem;
  max-width: 80rem;
}
.form__container {
  background-color: #484964;
  padding: 5rem;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  border-radius: 10px;
}
.form__heading {
  color: #ffffff;
  text-align: center;
}
.form__field {
  margin-bottom: 1rem;
}
.form__label {
  display: block;
  text-transform: uppercase;
  color: #ffffff;
  margin: 1rem 0;
}
.form__input {
  width: 100%;
  padding: 1.5rem;
  border: none;
}
.form__submit {
  margin-top: 2rem;
  background-color: #5896dd;
  font-size: 2rem;
  padding: 1.2rem 3rem;
  border-radius: 1rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #ffffff;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
  /* @include tablet {
    width: auto;
  } */
}
.form__submit:hover {
  cursor: pointer;
  background-color: #2e7bd4;
}
@media (min-width: 768px) {
  .form__submit {
    width: 100%;
  }
}

.error,
.success {
  text-align: center;
  text-transform: uppercase;
  font-weight: bold;
  margin-top: 2rem;
  margin-bottom: 0;
  font-size: 1.5rem;
}

.error {
  color: orange;
}

.success {
  color: #4af7ff;
}

.practice {
  padding: 5rem;
  max-width: 80rem;
}
.practice h1 {
  margin-bottom: 1.5rem;
  text-align: center;
}
.practice__board {
  height: 40rem;
  position: relative;
  transform-style: preserve-3d;
  transition: all 0.8s ease;
}
.practice__front, .practice__back {
  cursor: pointer;
  border-radius: 40px;
  padding: 12rem;
  box-shadow: 0 0px 20px 0 #686868;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.practice__front {
  background-color: #ffffff;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
  color: black;
  position: absolute;
  text-align: center;
}
.practice__back {
  background-color: #ffffff;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
  color: black;
  text-align: center;
  position: absolute;
  transform: rotateY(180deg);
}
.practice__next-prev-btns {
  text-align: center;
  margin-top: 1rem;
  display: flex;
  justify-content: space-evenly;
}
.practice__btn {
  background-color: #5896dd;
  font-size: 2rem;
  padding: 1.2rem 3rem;
  border-radius: 1rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #ffffff;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
  /* @include tablet {
    width: auto;
  } */
  padding: 0.2rem 1rem;
  font-size: 3rem;
}
.practice__btn:hover {
  cursor: pointer;
  background-color: #2e7bd4;
}

.flipCard {
  transform: rotateY(180deg);
}